import React, { useState } from "react";
import styled, { createGlobalStyle } from "styled-components";
import img from "../image/1.jpg";
import Body from "./Body";
import Result from "./Result";
createGlobalStyle`
    html,body{
        height:100%;
        width:100%;
    }
`;
const Container = styled.div`
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const Header = styled.div`
  width: 100%;
  height: 100%;
  background: #333 url(${img}) no-repeat center center/cover;
  /* color: white; */
  padding: 10vh 0;
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const Title = styled.h1`
  font-size: 32px;
  /* text-align:center; */
  color: white;
`;
const SearchBar = styled.input`
  width: 400px;
  height: 3rem;
  font-size: 1.2rem;
  font-weight: 500px;
  margin: 20px 0;
  border-radius: 30px;
  border: 3px solid rgba(24, 21, 50, 0.55);
  outline: none;
  padding: 0 1.5rem;
  color: #333;
  background-color: rgba(250, 250, 250, 0.55);
  box-shadow: 0px 0px 5px 5px rgba(0, 0, 0, 0.17);
`;

const useInputHandler = () => {
  const [result, setResult] = useState("");
  const [value, setValue] = useState("");
  const onKeyDown = e => {
    if (e.keyCode === 13) {
      setResult(result);
    }
  };
  const onChange = e => {
    const {
      target: { value }
    } = e;
    setValue(value);
  };
  return { result, value, onKeyDown, onChange };
};
export default () => {
  const searchBar = useInputHandler();
  const search = searchBar.result;
  return (
    <Container>
      <Header>
        <Title>FIND MUSIC . COM</Title>
        <SearchBar {...searchBar}></SearchBar>
      </Header>
      {search ? <Result search={search}></Result> : <Body></Body>}
    </Container>
  );
};
